import { LinearProgress } from "@mui/material";
import React, { lazy, Suspense } from "react";
import { Route, Routes } from "react-router-dom";
import "./App.css";

const LazyMainPage = lazy(() => import("./pages/main/MainPage"));
const LazyAboutUs = lazy(() => import("./pages/aboutUs/AboutUs"));
const LazyServices = lazy(() => import("./pages/services/Services"));
const LazyContact = lazy(() => import("./pages/contact/Contact"));

function App() {
  return (
    <div className="App">
      <Routes>
        <Route
          path="/*"
          element={
            <Suspense fallback={<LinearProgress color="success" />}>
              <LazyMainPage />
            </Suspense>
          }
          exact
        />
        <Route
          path="/hakkimda"
          element={
            <Suspense fallback={<LinearProgress color="success" />}>
              <LazyAboutUs />
            </Suspense>
          }
        />
        <Route
          path="/hizmetler"
          element={
            <Suspense fallback={<LinearProgress color="success" />}>
              <LazyServices />
            </Suspense>
          }
        />
        <Route
          path="/iletisim"
          element={
            <Suspense fallback={<LinearProgress color="success" />}>
              <LazyContact />
            </Suspense>
          }
        />
      </Routes>
    </div>
  );
}

export default App;
